.card {
  display: inline-block;
  width: 160px;
  height: 160px;
  margin: 4px;

  -webkit-transform-style: preserve-3d;

          transform-style: preserve-3d;

   /* animate rotate 180deg, that is, transform transitations */
  -webkit-transition: -webkit-transform 1s;
  transition: -webkit-transform 1s;
  -o-transition: transform 1s;
  transition: transform 1s;
  transition: transform 1s, -webkit-transform 1s;

  /* perspective: 800px; -- use why? why not? */
}
.card:hover {
    -webkit-box-shadow: 0 2px 2px DodgerBlue ;
            box-shadow: 0 2px 2px DodgerBlue ;
}
.card.shown {
    background-color: #FEFEFE;
    cursor: initial;
    font-size: 30px;
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}

.card-back,
.card-front {
  display: block;
  position: absolute;
  height: 100%;
  width: 100%;
  /* note: incl. margin+border in 100% calc - default is content-box */
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;

  border-radius: 10px;

  padding-top: 20px;
  text-align: center;
  font-size: 84px;
  background-color: DodgerBlue;
}

.card-front {
  border: 2px solid grey;
  -webkit-transform: rotateY( 180deg );
          transform: rotateY( 180deg );
}

.card-front .hint {
 font-size: 20px;
}

.card.shown .card-front {
    display: block;
}

.card-back {
 background-image: -webkit-gradient(linear,left top, left bottom,from(silver),to(grey));
 background-image: -webkit-linear-gradient(silver,grey);
 background-image: -o-linear-gradient(silver,grey);
 background-image: linear-gradient(silver,grey);
}



